const Article = (props) =>{
    const {author, content, description, title, urlToImage, url, source } = props.article;

    let image;

    if(urlToImage !== ''){
        image = <img src={urlToImage} alt={title} className='card-img-top'/>
    }
    return(
        <div className='col-md-6 col-12 mb-4'>

        </div>
    );
}

export default Article;